import React from 'react'
import { Link } from 'gatsby'
import { GoHome } from 'react-icons/go'

import { partPathFromNode, chapterPathFromNode } from '../helpers'

import Breadcrumb from 'react-bootstrap/Breadcrumb'

export default breadNav

function breadNav({ node }) {
  const isChapter = node.nChapter
  const isPart = node.nPart && !node.nChapter
  const isMatter = node.matter && !node.nPart
  const isClasse = node.classe && !node.matter

  let partString
  let partPath
  if (isPart || isChapter) {
    partString = `Partie ${node.nPart}: ${node.part}`
    partPath = partPathFromNode(node)
  }

  let chapterString
  let chapterPath
  if (isChapter) {
    chapterString = `Chapitre ${node.nChapter}: ${node.chapter}`
    // chapterPath = chapterPathFromNode(node)
  }

  return (
    <Breadcrumb>
      <Breadcrumb.Item>
        <Link to="/">
          <GoHome />
        </Link>
      </Breadcrumb.Item>

      <Breadcrumb.Item active={isClasse}>
        {isClasse ? (
          node.classe
        ) : (
          <Link to={`/` + node.classe}>{node.classe}</Link>
        )}
      </Breadcrumb.Item>

      {!isClasse && (
        <Breadcrumb.Item active={isMatter}>
          {isMatter ? (
            node.matter
          ) : (
            <Link to={`/` + node.classe + `/` + node.matter}>
              {node.matter}
            </Link>
          )}
        </Breadcrumb.Item>
      )}

      {!isClasse && !isMatter && (
        <Breadcrumb.Item active={isPart}>
          {isPart ? partString : <Link to={partPath}>{partString}</Link>}
        </Breadcrumb.Item>
      )}
      {isChapter && <Breadcrumb.Item active>{chapterString}</Breadcrumb.Item>}
    </Breadcrumb>
  )
}
