import React from 'react'

import { colors } from '../theme'

import Badge from 'react-bootstrap/Badge'

export default ({ node }) =>
  node.tags.map(function (tag, i) {
    const bcolor =
      colors.bootstrap[Math.floor(Math.random() * colors.bootstrap.length)]
    return (
      <Badge key={i} bg={bcolor}>
        {tag}
      </Badge>
    )
  })
