import React from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'

import Layout from '../components/layout'
import { PartPreview } from '../components/part-preview'
import BreadcrumbNav from '../components/breadcrumb-nav'
import MyJumbotron from '../components/jumbotron'
import CardGrid from '../components/card-grid'
import ArticlePreview from '../components/article-preview'

import {
  flattenFileNode,
  matterNameFromMatter,
  classeNameFromClasse,
} from '../helpers'

const Matter = ({ pageContext, data, location }) => {
  const { matter, classe } = pageContext

  //
  // Derniers cours
  //
  // aplat des objets
  let courseNodes = data.allMarkdownRemark.edges.map(flattenFileNode)

  // on filtre ici
  // TODO filtrer dans la requÃªte graphql
  courseNodes = courseNodes.filter(
    (node) => node.matter === matter && node.classe === classe
  )

  // On les classe par date dÃ©croissante
  courseNodes.sort((a, b) => {
    const dA = new Date(a.modifiedDate)
    const dB = new Date(b.modifiedDate)
    return dB - dA
  })
  courseNodes = courseNodes.slice(0, 3)

  //
  // parties
  //
  let partNodes = data.allSitePage.edges.map(flattenFileNode)

  // on filtre ici
  // TODO filtrer dans la requÃªte graphql
  partNodes = partNodes.filter(
    (node) => node.matter === matter && node.classe === classe
  )

  const totalCount = partNodes.length
  const matterHeader = `Cours en ${totalCount} partie${
    totalCount === 1 ? `` : `s`
  }`

  return (
    <Layout location={location} data={data}>
      <BreadcrumbNav node={pageContext} />
      <MyJumbotron>
        <h1>{`${matterNameFromMatter(matter)} ${classeNameFromClasse(
          classe
        )}`}</h1>
      </MyJumbotron>
      <h2
        className="matter-page display-4
      "
      >
        Derniers chapitres
      </h2>
      <CardGrid>
        {courseNodes.map((node) => (
          <ArticlePreview
            key={node.id}
            node={node}
            dataShowClasse
            dataShowMatter
            dataShowPart
            dataShowModifiedTime
          />
        ))}
      </CardGrid>
      <h2 className="matter-page display-4"> {matterHeader} </h2>
      <CardGrid>
        {partNodes.map((node) => (
          <PartPreview key={node.id} node={node} />
        ))}
      </CardGrid>
    </Layout>
  )
}

Matter.propTypes = {
  pageContext: PropTypes.shape({
    matter: PropTypes.string.isRequired,
    classe: PropTypes.string.isRequired,
  }),
  location: PropTypes.shape({
    pathname: PropTypes.string.isRequired,
  }),

  data: PropTypes.shape({
    allSitePage: PropTypes.shape({
      edges: PropTypes.array,
    }),
  }),
}

export default Matter

export const query = graphql`
  query MatterPage {
    allSitePage(
      filter: { component: { glob: "**/part.js" } }
      sort: { fields: [path] }
    ) {
      edges {
        node {
          id
          path
          pageContext
        }
      }
    }
    allMarkdownRemark(filter: { fields: { type: { eq: "course" } } }) {
      edges {
        node {
          id
          frontmatter {
            tags
          }
          fields {
            modifiedDate
            slug
            classe
            matter
            nPart
            part
            nChapter
            chapter
          }
        }
      }
    }
  }
`
