import React from 'react'
import { Link } from 'gatsby'

import ClasseBadge from '../components/classe-badge'
import MatterBadge from '../components/matter-badge'
import TagBadges from '../components/tag-badges'

import { dateOptions, partPathFromNode } from '../helpers'

import Card from 'react-bootstrap/Card'

export default ({
  node,
  dataShowClasse,
  dataShowMatter,
  dataShowPart,
  dataShowModifiedTime,
}) => {
  const Part = () => (
    <Link to={partPathFromNode(node)}>
      Partie {node.nPart}-{node.part}
    </Link>
  )
  const MatterPart = () => {
    if (dataShowClasse || dataShowMatter || dataShowPart) {
      return (
        <Card.Subtitle>
          {dataShowClasse && <ClasseBadge classe={node.classe} />}
          {dataShowMatter && <MatterBadge node={node} />}
          {dataShowPart && <Part />}
        </Card.Subtitle>
      )
    } else return null
  }

  return (
    <Card className="text-center">
      <Card.Header>
        <Card.Title>
          <Link to={`/` + node.slug}>
            Chapitre {node.nChapter}
            :&nbsp;
            {node.chapter}
          </Link>
        </Card.Title>
        <MatterPart />
      </Card.Header>
      <Card.Body>
        <TagBadges node={node} />
      </Card.Body>
      {dataShowModifiedTime && (
        <Card.Footer>
          <p className="text-muted text-right">
            <em>
              Mis Ã  jour le&nbsp;
              <time dateTime={node.modifiedDate}>
                {new Date(node.modifiedDate).toLocaleDateString(
                  `fr-FR`,
                  dateOptions
                )}
              </time>
            </em>
          </p>
        </Card.Footer>
      )}
    </Card>
  )
}
